import {useDispatch, useSelector} from "react-redux";
import {
    Avatar,
    Button,
    CircularProgress,
    Container, Dialog,
    Grid, ListItem, ListItemAvatar, ListItemText,
    Paper, Link,
    Typography, Divider, IconButton, DialogContent, DialogActions
} from "@mui/material";
import Web3 from "web3";
import coineus_abi from '../abis/coineus.json';

import {useEffect, useState} from "react";
import {makeBatchRequest} from "../utils/promisify";
import {
    updateWalletAVAXBalance,
    updateWalletBalance,
    updateWalletBNBBalance,
    updateWalletCoineusInfo,
    updateWalletFEGBalance,
    updateWalletFOOKBalance,
    updateWalletFOOKInfo,
    updateWalletFUDBalance,
    updateWalletFUDInfo,
    updateWalletFUSDBalance,
    updateWalletUSDCBalance
} from "../redux/slices/coineusSlice";
import {
    Link as RouterLink
} from "react-router-dom";

import BoltIcon from '@mui/icons-material/Bolt';

import cns_logo from "../images/logo192.png";
import fuse_logo from "../images/fuse_logo.png";
import fusd_logo from "../images/fusd_logo.png";
import avax_logo from "../images/avax_logo.png";
import usdc_logo from "../images/usdc_logo.png";
import fook_logo from "../images/fook_logo.jpeg";
import fud_logo from "../images/fud_logo.jpeg";
import bnb_logo from "../images/bnb_logo.png";
import feg_logo from "../images/feg_logo.png";
import consensus_abi from "../abis/consensus_abi.json";

import {toWei} from "../utils/format";
import CoineusLoading from "../components/CoineusLoading";
import TokenListItem from "../components/TokenListItem";
import {CoineusUSDFormat, CoineusCryptoFormat} from "../utils/currency_format";

export default function HomePage() {

    const dispatch = useDispatch();

    const {wallet, prices} = useSelector(state => state.coineus);

    const CoineusContractAddress = '0xc2E299b47398963c618De5b05C6BdeCd4cc64022';

    const [pending, setPending] = useState(false);

    const [stakeOpen, setStakeOpen] = useState(false)

    const [totalD, setTotalD] = useState(0);

    useEffect(() => {
        if (wallet.address) {
            getUserInfo();
        }
    }, [wallet.address]);

    //updates coineus info
    useEffect(() => {
        getCoineusInfo();
        const interval = setInterval(getCoineusInfo, 60000)
        return () => clearInterval(interval);
    }, [])

    const getCoineusInfo = () => {
        const web3 = new Web3('https://rpc.fuse.io');
        const contract = new web3.eth.Contract(coineus_abi, CoineusContractAddress);

        const calls = [
            contract.methods.getTotalDividendsDistributed().call
        ];

        makeBatchRequest(web3, calls).then(resp => {
            let [
                td
            ] = resp;

            setTotalD(td / (10 ** 18));
        });

    }

    const getUserInfo = () => {

        //FUSE
        const web3 = new Web3('https://rpc.fuse.io');
        web3.eth.getBalance(wallet.address).then(resp => {
            dispatch(updateWalletBalance(resp / (10 ** 18)))
        })

        const contract = new web3.eth.Contract(coineus_abi, CoineusContractAddress);
        const fusd_contract = new web3.eth.Contract(coineus_abi, '0x249be57637d8b013ad64785404b24aebae9b098b');

        const calls = [
            contract.methods.balanceOf(wallet.address).call,
            contract.methods.getAccountDividendsInfo(wallet.address).call,
            fusd_contract.methods.balanceOf(wallet.address).call
        ];

        makeBatchRequest(web3, calls).then(resp => {
            let [
                balance,
                info,
                fusd_balance
            ] = resp;

            let withdrawableDividends = info[3];
            let totalDividends = info[4];
            let secondsUntilAutoClaimAvailable = info[7];

            balance = balance / (10 ** 18);
            withdrawableDividends = withdrawableDividends / (10 ** 18);
            totalDividends = totalDividends / (10 ** 18);

            dispatch(updateWalletCoineusInfo({
                balance,
                withdrawableDividends,
                totalDividends,
                secondsUntilAutoClaimAvailable
            }));

            dispatch(updateWalletFUSDBalance(fusd_balance / (10 ** 18)));

        })

        //AVAX
        const web3AVAX = new Web3('https://api.avax.network/ext/bc/C/rpc');
        web3AVAX.eth.getBalance(wallet.address).then(resp => {
            dispatch(updateWalletAVAXBalance(resp / (10 ** 18)))
        })

        const fook_contract = new web3.eth.Contract(coineus_abi, '0x95923F63dA09B4f7520f7C65a31F318D8228B744');
        const fud_contract = new web3.eth.Contract(coineus_abi, '0x80363cc2CAa2545554d155031e47049ee2F98f33');
        const usdc_contract = new web3.eth.Contract(coineus_abi, '0xb97ef9ef8734c71904d8002f8b6bc66dd9c48a6e');

        const callsAVAX = [
            fook_contract.methods.balanceOf(wallet.address).call,
            fook_contract.methods.getAccountDividendsInfo(wallet.address).call,
            usdc_contract.methods.balanceOf(wallet.address).call,
            fud_contract.methods.balanceOf(wallet.address).call,
            fud_contract.methods.getAccountDividendsInfo(wallet.address).call,
        ];

        makeBatchRequest(web3AVAX, callsAVAX).then(resp => {
            let [
                balance,
                info,
                usdc_balance,
                fud_balance,
                fud_info
            ] = resp;

            let withdrawableDividends = info[3];
            let totalDividends = info[4];
            let secondsUntilAutoClaimAvailable = info[7];

            balance = balance / (10 ** 18);
            withdrawableDividends = withdrawableDividends / (10 ** 6);
            totalDividends = totalDividends / (10 ** 6);

            dispatch(updateWalletFOOKInfo({
                balance,
                withdrawableDividends,
                totalDividends,
                secondsUntilAutoClaimAvailable
            }));

            dispatch(updateWalletFOOKBalance(balance));
            dispatch(updateWalletUSDCBalance(usdc_balance / (10 ** 6)));

            // FUD TOKEN
            withdrawableDividends = fud_info[3];
            totalDividends = fud_info[4];
            secondsUntilAutoClaimAvailable = fud_info[7];

            fud_balance = fud_balance / (10 ** 18);
            withdrawableDividends = withdrawableDividends / (10 ** 6);
            totalDividends = totalDividends / (10 ** 6);

            dispatch(updateWalletFUDInfo({
                balance: fud_balance,
                withdrawableDividends,
                totalDividends,
                secondsUntilAutoClaimAvailable
            }));

            dispatch(updateWalletFUDBalance(fud_balance));

        })
            .catch(console.log)

        //BNB
        const web3BSC = new Web3('https://bsc-dataseed1.binance.org/');
        web3BSC.eth.getBalance(wallet.address).then(resp => {
            dispatch(updateWalletBNBBalance(resp / (10 ** 18)))
        })

        const feg_contract = new web3BSC.eth.Contract(coineus_abi, '0xacFC95585D80Ab62f67A14C566C1b7a49Fe91167');

        const callsBSC = [
            feg_contract.methods.balanceOf(wallet.address).call,
        ];

        makeBatchRequest(web3BSC, callsBSC).then(resp => {
            let [
                balance,
            ] = resp;

            dispatch(updateWalletFEGBalance(balance / (10 ** 9)));

        })
    }

    const claim = () => {
        if (wallet.address) {
            const web3 = new Web3(window.provider);
            const contract = new web3.eth.Contract(coineus_abi, CoineusContractAddress);

            contract.methods
                .claim()
                .send({from: wallet.address})
                .once('transactionHash', function (tx) {
                    setPending(true)
                })
                .on('error', function (error) {
                    setPending(false)
                })
                .then(function (receipt) {
                    getUserInfo();
                    setPending(false);
                });
        }
    }

    const claimFook = () => {
        if (wallet.address) {
            const web3 = new Web3(window.provider);
            const contract = new web3.eth.Contract(coineus_abi, '0x95923F63dA09B4f7520f7C65a31F318D8228B744');

            contract.methods
                .claim()
                .send({from: wallet.address})
                .once('transactionHash', function (tx) {
                    setPending(true)
                })
                .on('error', function (error) {
                    setPending(false)
                })
                .then(function (receipt) {
                    getUserInfo();
                    setPending(false);
                });
        }
    }

    const claimFud = () => {
        if (wallet.address) {
            const web3 = new Web3(window.provider);
            const contract = new web3.eth.Contract(coineus_abi, '0x80363cc2CAa2545554d155031e47049ee2F98f33');

            contract.methods
                .claim()
                .send({from: wallet.address})
                .once('transactionHash', function (tx) {
                    setPending(true)
                })
                .on('error', function (error) {
                    setPending(false)
                })
                .then(function (receipt) {
                    getUserInfo();
                    setPending(false);
                });
        }
    }

    return (<Container maxWidth="sm">

            <Grid container spacing={2} style={{marginTop: -5, paddingBottom: 20}}>
                <Grid item xs={12}>
                    <Paper style={{padding: 20, border: '1px solid #fbca00'}}>
                        <Typography color="primary" fontWeight={700}>New!</Typography>
                        <Typography>Projects for sale on Coineus.</Typography>
                        <Link to="/sale" component={RouterLink} underline="hover">View List</Link>
                    </Paper>
                </Grid>
                <Grid item xs={12}>
                    <Grid container spacing={2}>
                        <Grid item xs={6}>
                            <Paper>
                                <ListItem>
                                    <ListItemAvatar>
                                        <Avatar src={cns_logo}/>
                                    </ListItemAvatar>
                                    <ListItemText
                                        primary={CoineusUSDFormat(prices.cns?.inUSD)}
                                        primaryTypographyProps={{textAlign: 'right', fontWeight: 700}}
                                    />
                                </ListItem>
                            </Paper>
                        </Grid>
                        <Grid item xs={6}>
                            <Paper>
                                <ListItem>
                                    <ListItemAvatar>
                                        <Avatar src={fuse_logo}/>
                                    </ListItemAvatar>
                                    <ListItemText
                                        primary={CoineusUSDFormat(prices.fuse?.inUSD)}
                                        primaryTypographyProps={{textAlign: 'right', fontWeight: 700}}
                                    />
                                </ListItem>
                            </Paper>
                        </Grid>
                        <Grid item xs={6}>
                            <Paper>
                                <ListItem>
                                    <ListItemAvatar>
                                        <Avatar src={avax_logo}/>
                                    </ListItemAvatar>
                                    <ListItemText
                                        primary={CoineusUSDFormat(prices.avax?.inUSD)}
                                        primaryTypographyProps={{textAlign: 'right', fontWeight: 700}}
                                    />
                                </ListItem>
                            </Paper>
                        </Grid>
                        <Grid item xs={6}>
                            <Paper>
                                <ListItem>
                                    <ListItemAvatar>
                                        <Avatar src={bnb_logo}/>
                                    </ListItemAvatar>
                                    <ListItemText
                                        primary={CoineusUSDFormat(prices.bnb?.inUSD)}
                                        primaryTypographyProps={{textAlign: 'right', fontWeight: 700}}
                                    />
                                </ListItem>
                            </Paper>
                        </Grid>
                    </Grid>
                </Grid>
                <Grid item xs={12}>
                    <Paper style={{color: '#000', backgroundColor: '#fbca00'}}>
                        <ListItem>
                            <ListItemText
                                primary={`${totalD.toLocaleString('en-US', {
                                    maximumFractionDigits: 2,
                                    minimumFractionDigits: 2
                                })} fUSD` || '--'}
                                secondary="Total Rewards Distributed All-time"
                                primaryTypographyProps={{textAlign: 'right', fontWeight: 700, fontSize: '2.125rem'}}
                                secondaryTypographyProps={{textAlign: 'right', color: '#000'}}
                            />
                        </ListItem>
                    </Paper>
                </Grid>
                <Grid item xs={12}>
                    <Grid container spacing={2}>
                        <Grid item xs={3}>
                            <Button variant="contained" fullWidth
                                    href="https://app.voltage.finance/#/swap?inputCurrency=0xc2e299b47398963c618de5b05c6bdecd4cc64022"
                                    target="_blank">Swap</Button>
                        </Grid>
                        <Grid item xs={3}>
                            <Button variant="contained" fullWidth
                                    href="https://www.dextools.io/app/fuse/pair-explorer/0x0f2e60b799f2237633754f8eec7970c125e15d75"
                                    target="_blank">Chart</Button>
                        </Grid>
                        <Grid item xs={3}>
                            <Button variant="contained" fullWidth href="https://app.voltage.finance/#/bridge"
                                    target="_blank">Bridge</Button>
                        </Grid>
                        <Grid item xs={3}>
                            <Link to={"/staking"} component={RouterLink} style={{textDecoration: 'none'}}>
                                <Button variant="contained" fullWidth
                                        target="_blank">Stake</Button>
                            </Link>
                        </Grid>
                    </Grid>
                </Grid>
                <Grid item xs={12}>

                    <Divider>FUSE</Divider>

                    {/*FUSE*/}
                    <TokenListItem
                        name="FUSE"
                        logo={fuse_logo}
                        price={prices.fuse}
                        balance={wallet.balance}
                        secondaryAction={
                            <IconButton disabled={wallet.chain !== 122} edge="end" aria-label="stake" onClick={() => setStakeOpen(true)}>
                                <BoltIcon color={wallet.chain !== 122 ? "disabled" : "primary"}/>
                            </IconButton>
                        }
                    />
                    <PowerStake
                        open={stakeOpen}
                        amount={wallet.balance}
                        close={() => setStakeOpen(false)}
                        onSubmit={()=>{
                            setStakeOpen(false);
                            setPending(true);
                        }}
                        onSuccess={()=>{
                            getUserInfo();
                            setPending(false);
                        }}
                        onError={(err)=>{
                            setPending(false);
                        }}
                    />

                    {/*fUSD*/}
                    <TokenListItem
                        name="fUSD"
                        logo={fusd_logo}
                        network="fuse"
                        price={{inUSD: 1}}
                        balance={wallet.fusd_balance}
                    />

                    {/*CNS*/}
                    <TokenListItem
                        contract_address={CoineusContractAddress}
                        name="CNS"
                        logo={cns_logo}
                        network="fuse"
                        price={prices.cns}
                        balance={wallet.coineusInfo?.balance}
                        rewards={{
                            allTime: wallet.coineusInfo?.totalDividends,
                            pending: wallet.coineusInfo?.withdrawableDividends,
                            symbol: 'fUSD'
                        }}
                        rewardsClaim={
                            <ListItem style={{color: '#fbca00'}}>
                                <Button onClick={claim} variant='contained' fullWidth
                                        disabled={wallet.chain !== 122 || wallet.coineusInfo?.withdrawableDividends === 0 || !wallet.address || wallet.coineusInfo?.secondsUntilAutoClaimAvailable > 0}>{wallet.chain !== 122 ? 'Change to FUSE Chain' : `Claim ${wallet.coineusInfo?.secondsUntilAutoClaimAvailable > 0 ? 'Wait' : ''}`}</Button>
                            </ListItem>
                        }
                    />

                    <Divider>AVAX</Divider>

                    {/*AVAX*/}
                    <TokenListItem
                        name="AVAX"
                        logo={avax_logo}
                        price={prices.avax}
                        balance={wallet.avax_balance}
                    />

                    {/*USDC*/}
                    <TokenListItem
                        name="USDC"
                        logo={usdc_logo}
                        network="avax"
                        price={{inUSD: 1}}
                        balance={wallet.usdc_balance}
                    />

                    {/*FUD*/}
                    <TokenListItem
                        name="FUD"
                        logo={fud_logo}
                        network="avax"
                        price={prices.fud}
                        balance={wallet.fudInfo?.balance}
                        rewards={{
                            allTime: wallet.fudInfo?.totalDividends,
                            pending: wallet.fudInfo?.withdrawableDividends,
                            symbol: 'WAVAX'
                        }}
                        rewardsClaim={
                            <ListItem style={{color: '#fbca00'}}>
                                <Button onClick={claimFud} variant='contained' fullWidth
                                        disabled={
                                            wallet.chain !== 43114 ||
                                            wallet.fudInfo?.withdrawableDividends === 0 ||
                                            !wallet.address ||
                                            wallet.fudInfo?.secondsUntilAutoClaimAvailable > 0
                                        }>{wallet.chain !== 43114 ? 'Change to AVAX Chain' : `Claim ${wallet.fudInfo?.secondsUntilAutoClaimAvailable > 0 ? 'Wait' : ''}`}</Button>
                            </ListItem>
                        }
                    />

                    {/*FOOK*/}
                    <TokenListItem
                        name="FOOK"
                        logo={fook_logo}
                        network="avax"
                        price={prices.fook}
                        balance={wallet.fookInfo?.balance}
                        rewards={{
                            allTime: wallet.fookInfo?.totalDividends,
                            pending: wallet.fookInfo?.withdrawableDividends,
                            symbol: 'USDC'
                        }}
                        rewardsClaim={
                            <ListItem style={{color: '#fbca00'}}>
                                <Button onClick={claimFook} variant='contained' fullWidth
                                        disabled={
                                            wallet.chain !== 43114 ||
                                            wallet.fookInfo?.withdrawableDividends === 0 ||
                                            !wallet.address ||
                                            wallet.fookInfo?.secondsUntilAutoClaimAvailable > 0
                                        }>{wallet.chain !== 43114 ? 'Change to AVAX Chain' : `Claim ${wallet.fookInfo?.secondsUntilAutoClaimAvailable > 0 ? 'Wait' : ''}`}</Button>
                            </ListItem>
                        }
                    />

                    <Divider>BNB</Divider>

                    {/*BNB*/}
                    <TokenListItem
                        name="BNB"
                        logo={bnb_logo}
                        price={prices.bnb}
                        balance={wallet.bnb_balance}
                    />

                    {/*FEG*/}
                    <TokenListItem
                        name="FEG"
                        logo={feg_logo}
                        network="bnb"
                        price={prices.feg}
                        balance={wallet.feg_balance}
                    />
                </Grid>
            </Grid>

            <CoineusLoading open={pending} label="Transaction Pending"/>

        </Container>
    );
}


const coineusValidatorAddress = "0x53e2033F86C1a54bEbAE893DC8C9fAe179b3Faa2";
const consensusAddress = "0x3014ca10b91cb3D0AD85fEf7A3Cb95BCAc9c0f79";

function PowerStake(props) {

    const {wallet} = useSelector(state => state.coineus);
    const {open, close, amount, onSubmit, onSuccess, onError} = props;

    const delegate = () => {

        if (!wallet.address) return;

        const web3 = new Web3(window.provider);
        const connectedContract = new web3.eth.Contract(consensus_abi, consensusAddress)

        connectedContract.methods
            .delegate(coineusValidatorAddress)
            .send({
                from: wallet.address,
                value: toWei(amount - .1)
            })
            .once('transactionHash', function (tx) {
                onSubmit();
            })
            .on('error', function (error) {
                onError(error)
            })
            .then(function (receipt) {
                onSuccess()
            })
            .catch((error) => {
                onError(error)
            });
    }

    return <Dialog open={open} fullWidth maxWidth="sm">
        <DialogContent>
            <Typography>Stake <b>{(amount - .1).toLocaleString('en-US', {maximumFractionDigits: 8})}</b> FUSE</Typography>
        </DialogContent>
        <DialogActions>
            <Button onClick={close}>cancel</Button>
            <Button onClick={delegate} variant="contained">Stake</Button>
        </DialogActions>
    </Dialog>
}