import {Dialog, DialogContent, Grid, ListItem, ListItemText, Typography} from "@mui/material";
import {useEffect, useState} from "react";
import axios from "axios";

export default function TokenInfoDialog(props) {

    const { contract_address, open } = props;

    const [pairs, setPairs] = useState([]);

    useEffect(()=>{
        if(open && contract_address)fetchData()
    }, [open]);

    const fetchData = () => {
        // TODO change this to pull pair info
        axios
            .get(`https://api.dexscreener.com/latest/dex/tokens/${contract_address}`)
            .then( resp => {
                setPairs(resp.data.pairs);
            })
    }

    return <Dialog open={open} fullScreen>
        <DialogContent>
            {
                pairs.map(pair => <PairDetails data={pair} />)
            }

        </DialogContent>
    </Dialog>
}

function PairDetails(props) {

    const {data} = props;

    console.log(data);

    return <Grid container spacing={2}>
        <Grid item xs={12}>
            <Typography variant="caption">{data.pairAddress}</Typography>

        </Grid>
    </Grid>
}