import {Avatar, ListItem, ListItemAvatar, ListItemText, Paper} from "@mui/material";
import {CoineusUSDFormat, CoineusCryptoFormat} from "../utils/currency_format";
import clsx from "clsx";
import {makeStyles} from "@mui/styles";

import fuse_logo from "../images/fuse_logo.png";
import avax_logo from "../images/avax_logo.png";
import bnb_logo from "../images/bnb_logo.png";
import TokenInfoDialog from "./TokenInfoDialog";
import {useState} from "react";

const useStyles = makeStyles((theme) => ({
    networkChip: {
        borderRadius: "50%",
        marginLeft: 30,
        position: 'absolute',
        top: 42,
        height: 16,
        width: 16,
        display: 'inline-block',
        backgroundSize: 'cover',
        '&.fuse': {
            backgroundImage: `url('${fuse_logo}')`,
        },
        '&.avax': {
            backgroundImage: `url('${avax_logo}')`,
        },
        '&.bnb': {
            backgroundImage: `url('${bnb_logo}')`,
        }
    }
}));

export default function TokenListItem(props) {

    const classes = useStyles();

    const {contract_address, name, logo, network, price, balance, rewards, rewardsClaim} = props;

    const [open, setOpen] = useState(false)

    return <Paper style={{marginTop: 10, marginBottom:5}}>
        <ListItem secondaryAction={props.secondaryAction}>
            <ListItemAvatar onClick={()=>setOpen(true)}>
                <Avatar src={logo}/>
                {network && <span className={clsx(classes.networkChip, network)}/>}
            </ListItemAvatar>
            <ListItemText
                primary={name}
                secondary={CoineusUSDFormat(price?.inUSD)}
                primaryTypographyProps={{fontWeight: 700}}
            />
            <ListItemText
                primary={CoineusCryptoFormat(balance)}
                secondary={CoineusUSDFormat(price?.inUSD * balance)}
                primaryTypographyProps={{textAlign: 'right', fontWeight: 700}}
                secondaryTypographyProps={{textAlign: 'right'}}
            />
        </ListItem>

        {
            rewards && <ListItem style={{color: '#fbca00'}}>
                <ListItemText
                    primary='Rewards All-time'
                    secondary='Pending'
                    primaryTypographyProps={{fontWeight: 700}}
                />
                <ListItemText
                    primary={`${CoineusCryptoFormat(rewards.allTime)} ${rewards.symbol}`}
                    secondary={`${CoineusCryptoFormat(rewards.pending)} ${rewards.symbol}`}
                    primaryTypographyProps={{textAlign: 'right', fontWeight: 700}}
                    secondaryTypographyProps={{textAlign: 'right'}}
                />
            </ListItem>
        }
        {rewardsClaim}

        {/*TODO work on token details*/}
        <TokenInfoDialog contract_address={contract_address} open={false} close={()=>setOpen(false)} />
    </Paper>
}